<template>
  <b-row>
      <b-col lg="6">
        <b-row>
        <c-table :table-data="items" :fields="fields" caption="Product Details"></c-table>
        </b-row>
        <b-row>
          <b-card>
            <b-card-header>
              <i class="icon-menu mr-1"></i>Join Requests
            </b-card-header>
            <b-card-body>
              <v-client-table :columns="columns" :data="data1"  :options="options" :theme="theme" id="dataTable" >
                <div slot="child_row" slot-scope="props">
                  <b-form v-on:submit.prevent="updateDatajoin(props.row._id)">

                    <b-button v-on:click="delDatajoin(props.row._id)" type="submit" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i>Accept</b-button>
                  </b-form>

                    <b-button v-on:click="delDatajoin(props.row._id)" type="submit" size="sm" variant="danger"><i class="fa fa-ban"></i> Reject</b-button>

                </div>
              </v-client-table>
            </b-card-body>
          </b-card>
        </b-row>
      </b-col>

      <b-col lg="6">
        <b-card>
          <div slot="header">
            <strong>Update/Delete</strong> Inventory
          </div>
          <b-form v-on:submit.prevent="updateData">

            <b-form-group
              label="Current Site Id"
              label-for="basicInputDisabled"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicInputDisabled" type="text" :value="chats.room_site" :disabled="true" placeholder="Site Id"></b-form-input>
            </b-form-group>
            <b-form-group
              description="Enter the Site"
              label=" Update Site Id"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">



              <cool-select
                v-model="chats.room_site"
                :items="items1"
                :loading="loading"
                item-text="siteid"
                item-value="_id"
                placeholder="Just Type The Site ID/Name"
                disable-filtering-by-search
                @search="onSearch"
                >
              </cool-select>
            </b-form-group>
            <b-form-group
              description="Enter the Room Name"
              label="Room Name"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-form-input id="basicText" v-model="chats.room_name" type="text" placeholder="Enter the room Name"></b-form-input>
            </b-form-group>

            <b-form-group
            label=" Current Allowed Users"
            label-for="tags"
            :label-cols="3"
            :horizontal="true">
            <b-col cols="13" v-for="(run, index) in test.allowed_users">
              <b-form-input :disabled="true"  :value="test.allowed_users[index]"></b-form-input>
            </b-col>
          </b-form-group>
            <b-form-group
              description="Allowed Users"
              label="Allowed Users"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-col cols="13" v-for="(run, index) in chats.allowed_users" :key="index">
              <cool-select
                v-model="chats.allowed_users[index]"
                :items="items2"
                :loading="loading"
                item-text="name"
                placeholder="Enter The User ID"
                disable-filtering-by-search
                @search="onSearch2"
                >
              </cool-select><br>
            </b-col>
            </b-form-group>
            <b-button class="float-right"  @click="deleteuser()" size="sm" variant="danger"><i class="fa fa-ban"></i> - Delete User</b-button>
            <b-button class="float-right" type="button" @click="adduser()" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> + Add User</b-button>
            <br><br>


            <b-form-group
            label=" Current Group Admins"
            label-for="tags"
            :label-cols="3"
            :horizontal="true">
            <b-col cols="13" v-for="(run, index) in test.group_admins">
              <b-form-input :disabled="true"  :value="test.group_admins[index]"></b-form-input>
            </b-col>
          </b-form-group>
            <b-form-group
              description="New Group Admins"
              label="Group Admins"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-col cols="13" v-for="(run, index) in chats.group_admins" :key="index">

              <cool-select
                v-model="chats.group_admins[index]"
                :items="items3"
                :loading="loading"
                item-text="name"
                placeholder="Enter The Admin ID"
                disable-filtering-by-search
                @search="onSearch3"
                >
              </cool-select><br>
              </b-col>
            </b-form-group>
            <b-button class="float-right"  @click="deleteadmin()" size="sm" variant="danger"><i class="fa fa-ban"></i> - Delete Admin</b-button>
            <b-button class="float-right" type="button" @click="addadmin()" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> + Add Admin</b-button>
            <br><br>

            <b-form-group
              label="Only Admins can Send Messages"
              label-for="basicRadiosCustom"
              :label-cols="3"
              :horizontal="true">
              <b-form-radio-group
                id="basicRadiosCustom"
                value="1"
                stacked>
                <div class="custom-control custom-radio">
                  <input type="radio" v-model="chats.only_admins" id="customRadio1" name="customRadio" class="custom-control-input" value="yes">
                  <label class="custom-control-label" for="customRadio1">Yes</label>
                </div>
                <div class="custom-control custom-radio">
                  <input type="radio" v-model="chats.only_admins" id="customRadio2" name="customRadio" class="custom-control-input" value="no" checked>
                  <label class="custom-control-label" for="customRadio2">No</label>
                </div>
              </b-form-radio-group>
            </b-form-group>


            <b-form-group
            label="Current Emergency Contacts"
            label-for="tags"
            :label-cols="3"
            :horizontal="true">
            <b-col cols="13" v-for="(run, index) in test.emergency_contacts">
              <b-form-input :disabled="true"  :value="test.emergency_contacts[index]"></b-form-input>
            </b-col>
          </b-form-group>


            <b-form-group
              description="New Emergency Contacts"
              label="New Emergency Contacts"
              label-for="basicText"
              :label-cols="3"
              :horizontal="true">
              <b-col cols="13" v-for="(run, index) in chats.emergency_contacts" :key="index">

              <cool-select
                v-model="chats.emergency_contacts[index]"
                :items="items4"
                :loading="loading"
                item-text="name"
                placeholder="Enter The Contacts ID"
                disable-filtering-by-search
                @search="onSearch4"
                >
              </cool-select><br>
              </b-col>
            </b-form-group>
            <b-button class="float-right"  @click="deletecontact()" size="sm" variant="danger"><i class="fa fa-ban"></i> - Delete Contact</b-button>
            <b-button class="float-right" type="button" @click="addcontact()" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> + Add Contact</b-button>
            <br><br><br>






          <div slot="footer">
            <b-button type="submit" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
            <b-button v-on:click="delData()" type="submit" size="sm" variant="danger"><i class="fa fa-ban"></i> Reset</b-button>
          </div>
          </b-form>
        </b-card>
        </b-col>
    </b-row>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import cTable from './Table.vue'
  import {ClientTable, Event} from 'vue-tables-2'
  import { CoolSelect } from 'vue-cool-select'
  var link = window.location.href
  var key = link.split("chatgroups/",24).pop()
  console.log(key)
  Vue.use(ClientTable)
export default {
  name: 'dashboard',
  components: {
    cTable,
    CoolSelect,
    ClientTable,
    Event
  },
  data: function () {
    return {
      items:[],
      data: [],
      joinData : [],
      fields: [
        {key: 'room_code', label: 'Room Code', sortable: true},
        {key: 'created_date',},
        {key: 'room_name'},
        {key: 'total_join_req'},
        {key: 'no_of_users'},
      ],
      columns: ['username','room_name','room_code','_id'],
        data1 : [],
        options: {
          headings: {
            username : 'Username',
            room_name : 'Room Name',
            room_code : 'Room Code',
            _id : 'URL'
          },
          sortable: ['username','room_name','room_code'],
          filterable: ['username','room_name','room_code'],
          sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
          pagination: {
            chunk: 5,
            edge: false,
            nav: 'scroll'
          }
        },
        useVuex: false,
        theme: 'bootstrap4',
        template: 'default',
      selected: null,
      items1: [],
      items2:[],
      items3:[],
      items4:[],
      test:[],
      loading: false,
      timeoutId: null,
      noData: false,
      // inventory: [],
      errors: [],
      chats: {
        room_name: "",
        room_site: "",
        allowed_users: [],
        group_admins: [],
        only_admins:"",
        emergency_contacts:[],
        _id: key,
      },
      response: "",
      join: {
        username: "",
        room_code: "",
        user: "",
        room_id: "",
        user_token: "",
        _id: key,
      },

    }
  },
  async mounted() {
    await axios.get("https://selacious-cloud-siteapi.herokuapp.com/join")
    .then(
      response => {this.joinData = response.data
        axios.get(`https://selacious-cloud-siteapi.herokuapp.com/chats/${key}`)
        .then(
          resp =>{
            this.chats = resp.data
            this.test = resp.data
            this.data = resp.data
            var count =0;
            this.data.no_of_users = this.data.allowed_users.length
            for(var j=0;j<this.joinData.length;j++){
              if(this.data.room_code == this.joinData[j].room_code){
                count = count+1;
                this.data1.push(this.joinData[j])
              }
            }
            this.data.total_join_req = count
            this.items = [this.data]
          })

    })
  },
  methods: {
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    flag (value) {
      return 'flag-icon flag-icon-' + value
    },
    async delDatajoin(id) {
                axios({ method: "DELETE", "url": `https://selacious-cloud-siteapi.herokuapp.com/join/${id}`, "data": this.j, "headers": { 'Content-Type': 'application/x-www-form-urlencoded' } }).then(result => {
                    this.join = result.data;
                    this.$router.push(`/chatgroups`);
                }, error => {
                    console.error(error);
                });
    },
    async updateDatajoin(id) {
      axios.post(`https://pv-chat-socket.herokuapp.com/app/send/notification/`,{user_token:this.join.user_token}).then((response) =>{
        console.log(response);
      });
      axios.post(`https://selacious-cloud-siteapi.herokuapp.com/chats/join/`,{_id:id,allowed_users:this.join.user}).then((response) =>{
        console.log(response);
        this.$router.push(`/chatgroups`);
      });
    },
    async updateData() {
      axios.put(`https://selacious-cloud-siteapi.herokuapp.com/chats/${key}`,this.chats).then((response) =>{
        console.log(response);
        this.$router.push("/chatgroups");
      });
    },
    async delData() {
                axios({ method: "DELETE", "url": `https://selacious-cloud-siteapi.herokuapp.com/chats/${key}`, "data": this.chat, "headers": { 'Content-Type': 'application/x-www-form-urlencoded' } }).then(result => {
                    this.chats = result.data;
                    this.$router.push("/chatgroups");
                }, error => {
                    console.error(error);
                });
    },
    async onSearch(search) {
    const lettersLimit = 2;
    this.noData = false;
    if (search.length < lettersLimit) {
      this.items1 = [];
      this.loading = false;
      return;
    }
    this.loading = true;
    clearTimeout(this.timeoutId);
    this.timeoutId = setTimeout(async () => {
      const response = await fetch(
        `https://selacious-cloud-siteapi.herokuapp.com/sites`
      );
      this.items1 = await response.json();
      this.loading = false;
      if (!this.items1.length) this.noData = true;
      console.log(this.items1);
    }, 500);
  },
  async onSearch2(search) {
  const lettersLimit = 2;
  this.noData = false;
  if (search.length < lettersLimit) {
    this.items2 = [];
    this.loading = false;
    return;
  }
  this.loading = true;
  clearTimeout(this.timeoutId);
  this.timeoutId = setTimeout(async () => {
    const response = await fetch(
      `https://selacious-cloud-siteapi.herokuapp.com/users`
    );
    this.items2 = await response.json();
    this.loading = false;
    if (!this.items2.length) this.noData = true;
    console.log(this.items2);
  }, 500);
},
async onSearch3(search) {
const lettersLimit = 2;
this.noData = false;
if (search.length < lettersLimit) {
  this.items3 = [];
  this.loading = false;
  return;
}
this.loading = true;
clearTimeout(this.timeoutId);
this.timeoutId = setTimeout(async () => {
  const response = await fetch(
    `https://selacious-cloud-siteapi.herokuapp.com/users`
  );
  this.items3 = await response.json();
  this.loading = false;
  if (!this.items3.length) this.noData = true;
  console.log(this.items3);
}, 500);
},
async onSearch4(search) {
const lettersLimit = 2;
this.noData = false;
if (search.length < lettersLimit) {
  this.items4 = [];
  this.loading = false;
  return;
}
this.loading = true;
clearTimeout(this.timeoutId);
this.timeoutId = setTimeout(async () => {
  const response = await fetch(
    `https://selacious-cloud-siteapi.herokuapp.com/users`
  );
  this.items4 = await response.json();
  this.loading = false;
  if (!this.items4.length) this.noData = true;
  console.log(this.items4);
}, 500);
},
adduser: function () {
this.chats.allowed_users.push("");
},
deleteuser: function(index) {
this.chats.allowed_users.splice(index, 1);
this.test.allowed_users.splice(index, 1);
},
addadmin: function () {
this.chats.group_admins.push("");
},
deleteadmin: function(index) {
this.chats.group_admins.splice(index, 1);
this.test.group_admins.splice(index, 1);
},
addcontact: function () {
this.chats.emergency_contacts.push("");
},
deletecontact: function(index) {
this.chats.emergency_contacts.splice(index, 1);
this.test.emergency_contacts.splice(index, 1);
},
  }
};
</script>

<style lang="scss">
  #dataTable {
    width: 95%;
    margin: 0 auto;
    .VuePagination {
      text-align: center;
      justify-content: center;
    }
    .vue-title {
      text-align: center ;
      margin-bottom: 10px;
    }
    .VueTables__search-field {
      display: flex;
    }
    .VueTables__search-field input {
      margin-left: 0.25rem;
    }
    .VueTables__limit-field {
      display: flex;
    }
    .VueTables__limit-field select {
      margin-left: 0.25rem !important;
    }
    .VueTables__table th {
      text-align: center;
    }
    .VueTables__child-row-toggler {
      width: 16px;
      height: 16px;
      line-height: 16px;
      display: block;
      margin: auto;
      text-align: center;
    }
    .VueTables__child-row-toggler--closed::before {
      content: "+";
    }
    .VueTables__child-row-toggler--open::before {
      content: "-";
    }
  }
</style>
